/*--------------------------------------------------------------
# Modules
--------------------------------------------------------------*/
.ftf-module {
  position: relative;
}
.ftf-module .module__content p:last-child {
  margin-bottom: 0;
}

/*--------------------------------------------------------------
- Announcement Module
--------------------------------------------------------------*/
.module-announcement {
  background-size: cover;
}
.module-announcement__close {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
.module-announcement.js-is-sticky-yes {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99999;
}
.admin-bar .module-announcement.js-is-sticky-yes {
  top: 32px;
}

@media screen and (max-width: 782px) {
  .admin-bar .module-announcement.js-is-sticky-yes {
    position: absolute;
    top: 46px;
  }
}
/*--------------------------------------------------------------
- Hero Module
--------------------------------------------------------------*/
.module-hero .module__title {
  color: inherit;
}
.module-hero__product-title {
  font-weight: 600;
  padding-bottom: 0.3125rem;
  border-bottom: 1px solid;
}
.module-hero__video {
  margin: 0;
}
.module-hero__video-image-wrap {
  position: relative;
}
.module-hero__video-image {
  position: relative;
  z-index: 1;
  display: block;
}
.module-hero__video-play-buttton {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 300ms ease-in-out;
}
.module-hero__video-caption {
  display: block;
  text-align: center;
  margin-top: 1em;
  font-size: 0.875rem;
  opacity: 0.8;
}

/*--------------------------------------------------------------
- CTA Module
--------------------------------------------------------------*/
.module-cta {
  text-align: center;
}

/*--------------------------------------------------------------
- Testimonials Carousel Module
--------------------------------------------------------------*/
.module-testimonials-carousel .swiper-slide .row {
  box-sizing: border-box;
}
.module-testimonials-carousel .testimonial__author {
  font-size: 1.125rem;
}
.module-testimonials-carousel .testimonial__avatar {
  display: block;
  border-radius: 50%;
}